import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_BOTTOM = mu.marginBottom();

import Error from '../../components/error';
import Message from '../../components/message';

@observer
class Profile extends Component {
	constructor(props) {
        super(props);
        this.state = {
            time: Date.now(),
			count: 10
        };
    }

	resetEmailMessage = () => {
		const { userStore } = this.props.stores;
		userStore.updateField('emailMessage', '');
		userStore.updateField('emailMessageIcon', '');
	}

	resetEmailError = () => {
		const { userStore } = this.props.stores;
		userStore.updateField('emailError', '');
		userStore.updateField('emailErrorIcon', '');
	}

	toggleSetting= async (field) => {
        const { userStore } = this.props.stores;
		const { user } = userStore;
		const { settings = {} } = user;
        await userStore.updateSetting({ [`settings.${field}`]: settings[field] === 1 ? 0 : 1 });
    }

	componentDidMount() {
		const { userStore } = this.props.stores;

		if (this.props.verifyEmailToken) {
			userStore.verifyEmail({ token: this.props.verifyEmailToken });
		}
	}

	render() {
		const { userStore } = this.props.stores;
		const { user, emailMessage, emailError, emailMessageIcon, emailErrorIcon, isAdmin } = userStore;
		const { teams, settings = {} } = user;
		const allTeams = userStore.findTeams(teams);
		const apiServer = util.getApiServer();
		const jwtToken = util.getJwtToken();
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='row'>
					<div class='col-12'>
						<Error message={emailError} icon={emailErrorIcon} remove={this.resetEmailError} />
                        <Message message={emailMessage} icon={emailMessageIcon} remove={this.resetEmailMessage}  />

						<a href='/users/edit' class='btn btn-primary btn-sm float-right'>
							<Text id='users.edit'>Endre</Text>
						</a>

						<h5><Text id='users.title'>Din profil</Text></h5>
					</div>
					<div class='col-4 text-muted text-right'><Text id='users.email'>E-post</Text></div><div class='col-8'>{user.email}</div>

					<div class='col-4 text-muted text-right'><Text id='users.password'>Passord</Text></div><div class='col-8'>
						<a href='/users/edit-password' class='btn btn-primary btn-sm'>
							<Text id='users.change-password'>Endre passord</Text>
						</a>
					</div>
				
					{user.team && <><div class='col-4 text-muted text-right'><Text id='users.main-team'>Hovedteam</Text></div><div class='col-8'>{userStore.findTeam(user.team)}</div></>}
					{user.teams && <><div class='col-4 text-muted text-right'><Text id='users.all-teams'>Alle teamene</Text></div><div class='col-8'>{allTeams.map(team => (
						<div>
							{team.name}
							{/* <span class='text-muted'>
								<i class='fas fa-dog ml-3' /> {team.dogs.length}
								<i class='fas fa-users ml-3' /> {team.members.length}
							</span> */}
						</div>
					))}</div></>}
					{user.username && <><div class='col-4 text-muted text-right'><Text id='users.username'>Brukernavn</Text></div><div class='col-8'>{user.username}</div></>}
					{user.firstname && <><div class='col-4 text-muted text-right'><Text id='users.firstname'>Fornavn</Text></div><div class='col-8'>{user.firstname}</div></>}
					{user.lastname && <><div class='col-4 text-muted text-right'><Text id='users.lastname'>Etternavn</Text></div><div class='col-8'>{user.lastname}</div></>}
					{user.cellphone && <><div class='col-4 text-muted text-right'><Text id='users.cellphone'>Mobil</Text></div><div class='col-8'>{user.cellphone}</div></>}
					{user.url && <><div class='col-4 text-muted text-right'><Text id='users.homepage'>Hjemmeside</Text></div><div class='col-8'>{user.url}</div></>}
					{user.facebook && <><div class='col-4 text-muted text-right'><Text id='users.facebook'>Facebook</Text></div><div class='col-8'>{user.facebook}</div></>}
					{user.instagram && <><div class='col-4 text-muted text-right'><Text id='users.instagram'>Instagram</Text></div><div class='col-8'>{user.instagram}</div></>}
					{user.snapchat && <><div class='col-4 text-muted text-right'><Text id='users.snapchat'>Snapchat</Text></div><div class='col-8'>{user.snapchat}</div></>}
				</div>

				<div class='row mt-4'>
					<div class='col-4 text-muted text-right'>
						<Text id='users.settings-hide-profile'>Skjul profilen</Text>
					</div>
					<div class='col-8'>
						<div class='custom-control custom-switch'>
							<input type='checkbox' class='custom-control-input' id='settingsHideProfile' onInput={() => this.toggleSetting('hideProfile')} checked={settings.hideProfile} />
							<label class='custom-control-label' for='settingsHideProfile' />
						</div>
						<small class='text-muted'><Text id='users.settings-hide-profile-help'>Det vil snart være mulig å bli venner med andre her på The Musher. Dersom du ikke vil at andre skal kunne finne deg, så kan du skjule profilen din her.</Text></small>
					</div>
				</div>

				<div class='row mt-4'>
					<div class='col-4 text-muted text-right'>
						<Text id='users.settings-hide-invite'>Skjul invitasjonsboks</Text>
					</div>
					<div class='col-8'>
						<div class='custom-control custom-switch'>
							<input type='checkbox' class='custom-control-input' id='settingsHideInvitation' onInput={() => this.toggleSetting('hideInvite')} checked={settings.hideInvite} />
							<label class='custom-control-label' for='settingsHideInvitation' />
						</div>
						<small class='text-muted'><Text id='users.settings-hide-invite-help'>Skjul invitasjonsboksen på forsiden.</Text></small>
					</div>
				</div>

				<div class='row mt-4'>
					<div class='col-4 text-muted text-right'>
						<Text id='users.settings-hide-settings'>Skjul innstillinger</Text>
					</div>
					<div class='col-8'>
						<div class='custom-control custom-switch'>
							<input type='checkbox' class='custom-control-input' id='settingsHideSettings' onInput={() => this.toggleSetting('hideSettings')} checked={settings.hideSettings} />
							<label class='custom-control-label' for='settingsHideSettings' />
						</div>
						<small class='text-muted'><Text id='users.settings-hide-settings-help'>Skjul innstillinger på forsiden.</Text></small>
					</div>
				</div>

				<div class='row mt-4'>
					<div class='col-4 text-muted text-right'>
						<Text id='users.settings-summary-email'>Oppsummeringer</Text>
					</div>
					<div class='col-8'>
						<div class='custom-control custom-switch'>
							<input type='checkbox' class='custom-control-input' id='settingsSummaryEmails' onInput={() => this.toggleSetting('summaryEmails')} checked={settings.summaryEmails} />
							<label class='custom-control-label' for='settingsSummaryEmails' />
						</div>
						<small class='text-muted'><Text id='users.settings-receive-summary-email'>Motta oppsummeringer via e-post</Text></small>
					</div>
				</div>

				<div class='row mt-4'>
					<div class='col-4 text-muted text-right'>
						<Text id='users.settings-info-email'>Info</Text>
					</div>
					<div class='col-8'>
						<div class='custom-control custom-switch'>
							<input type='checkbox' class='custom-control-input' id='settingsInfoEmails' onInput={() => this.toggleSetting('infoEmails')} checked={settings.infoEmails} />
							<label class='custom-control-label' for='settingsInfoEmails' />
						</div>
						<small class='text-muted'><Text id='users.settings-receive-info-email'>Motta info via e-post</Text></small>
					</div>
				</div>

				<div class='row mt-4'>
					<div class='col-4 text-muted text-right'>
						<Text id='users.settings-partner-email'>Partners</Text>
					</div>
					<div class='col-8'>
						<div class='custom-control custom-switch'>
							<input type='checkbox' class='custom-control-input' id='settingsPartnerEmails' onInput={() => this.toggleSetting('partnerEmails')} checked={settings.partnerEmails} />
							<label class='custom-control-label' for='settingsPartnerEmails' />
						</div>
						<small class='text-muted'><Text id='users.settings-receive-partner-email'>Motta tilbud fra våre partnere via e-post</Text></small>
					</div>
				</div>

				<div class='row border-top mt-5 pt-5'>
					<div class='col-12'>
						<div>
							<img src='./assets/strava_logo_orange.png' style='max-height: 100px;' />
						</div>
					</div>
					{user.strava && user.strava.access_token ? (
						<>
							<div class='col-12'><h5><i class='fas fa-thumbs-up text-success' /> <Text id='users.strava-connected'>Connected to Strava!</Text></h5></div>
							<div class='col-4 text-right text-muted'><Text id='users.strava-id'>id</Text>:</div><div class='col-8'> {user.strava.athlete.id}</div>
							<div class='col-4 text-right text-muted'><Text id='users.strava-username'>username</Text>:</div><div class='col-8'> {user.strava.athlete.username}</div>
							{/* <div class='col-4 text-right text-muted'>resource_state:</div><div class='col-8'> {user.strava.athlete.resource_state}</div>
							<div class='col-4 text-right text-muted'>firstname:</div><div class='col-8'> {user.strava.athlete.firstname}</div>
							<div class='col-4 text-right text-muted'>lastname:</div><div class='col-8'> {user.strava.athlete.lastname}</div>
							<div class='col-4 text-right text-muted'>city:</div><div class='col-8'> {user.strava.athlete.city}</div>
							<div class='col-4 text-right text-muted'>state:</div><div class='col-8'> {user.strava.athlete.state}</div>
							<div class='col-4 text-right text-muted'>country:</div><div class='col-8'> {user.strava.athlete.country}</div>
							<div class='col-4 text-right text-muted'>sex:</div><div class='col-8'> {user.strava.athlete.sex}</div>
							<div class='col-4 text-right text-muted'>premium:</div><div class='col-8'> {user.strava.athlete.premium}</div>
							<div class='col-4 text-right text-muted'>summit:</div><div class='col-8'> {user.strava.athlete.summit}</div>
							<div class='col-4 text-right text-muted'>created_at:</div><div class='col-8'> {user.strava.athlete.created_at}</div>
							<div class='col-4 text-right text-muted'>updated_at:</div><div class='col-8'> {user.strava.athlete.updated_at}</div>
							<div class='col-4 text-right text-muted'>badge_type_id:</div><div class='col-8'> {user.strava.athlete.badge_type_id}</div>
							<div class='col-4 text-right text-muted'>profile_medium:</div><div class='col-8'> <img src={user.strava.athlete.profile_medium} /></div> */}
							<div class='col-4 text-right text-muted'><Text id='users.strava-profile'>profile</Text>:</div><div class='col-8'> <img src={user.strava.athlete.profile} /></div>
							{/* <div class='col-4 text-right text-muted'>friend:</div><div class='col-8'> {user.strava.athlete.friend}</div>
							<div class='col-4 text-right text-muted'>follower:</div><div class='col-8'> {user.strava.athlete.follower}</div> */}

							{/* {isAdmin && <div class='col-12'>
								<button class='btn btn-primary' onclick={userStore.stravaGetActivityStream}><Text id='users.strava-get-activities'>Get activity streams</Text></button>
							</div>} */}
							<div class='col-12 mt-2'>
								<div class='display-1 text-success'>
									<i class='fas fa-check' />
								</div>
								<small class='text-muted'>
									<small>
										<Text id='users.strava-reconnect-help'>Hvis du opplever problemer med integrasjoen så kan du prøve å koble til på nytt</Text>:<br />
										<a href={`${apiServer}/api/strava/request-access?token=${jwtToken}`} native>
											<Text id='users.strava-reconnect'>Reconnect to Strava</Text>
										</a>
									</small>
								</small>
							</div>
						</>
					) : (
						<>
							<div class='col-12 mt-2'>
								<a class='btn btn-primary' href={`${apiServer}/api/strava/request-access?token=${jwtToken}`} native><Text id='users.strava-connect'>Connect to Strava</Text></a>
							</div>
						</>
					)}
				</div>
				<div class='row border-top mt-5 pt-5'>
					<div class='col-12'>
						<div>
							<img src='./assets/garmin-connect.png' style='height: 100px;' />
						</div>
					</div>
					{user.garmin && user.garmin.access && user.garmin.access.oauth_token ? (
						<>
							<div class='col-12'>
								<h5><i class='fas fa-thumbs-up text-success' /> <Text id='users.garmin-connected'>Connected to Garmin Connect!</Text></h5>
							</div>

							<div class='col-12 mt-2'>
								<div class='display-1 text-success'>
									<i class='fas fa-check' />
								</div>
								<small class='text-muted'>
									<small>
										<Text id='users.garmin-reconnect-help'>Hvis du opplever problemer med integrasjoen så kan du prøve å koble til på nytt</Text>:<br />
										<a href={`${apiServer}/api/garmin/request-access?token=${jwtToken}`} native>
											<Text id='users.garmin-reconnect'>Reconnect to Garmin Connect</Text>
										</a>
									</small>
								</small>
							</div>
						</>
					) : (
						<>
							<div class='col-12 mt-2'>
								<a class='btn btn-primary' href={`${apiServer}/api/garmin/request-access?token=${jwtToken}`} native><Text id='users.garmin-connect'>Connect to Garmin Connect</Text></a>
							</div>
						</>
					)}
				</div>
			</div>
		);
	}
}

export default Profile;
